* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.back {
  background: rgb(148, 227, 238);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
}

.card {
  background: rgb(255, 255, 255);
  display: flex;
  flex-direction: column;
  width: 400px;
  height: 500px;
  border-radius: 30px;
  box-shadow: 5px 5px 8px rgba(18, 47, 126, 0.432);
}

h1 {
  background: rgba(120, 167, 185, 0.623);
  color: azure;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.548);
  margin: 30px 0px;
  width: 100%;
  display: flex;
  justify-content: center;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.486);
}

.top {
  display: flex;
  justify-content: space-between;
  padding: 0px 10px;
}

input {
  padding: 8px;
  width: 100%;
  border-color: rgb(100, 98, 98);
}

.button1 {
  background-color: rgba(148, 227, 238, 0.493);
  color: rgb(140, 150, 160);
  margin-left: 20px;
  border-radius: 50%;
  width: 60px;
  height: 50px;
  box-shadow: none;
  font-size: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-color: rgb(0, 0, 0);
  font-weight: 900;
}

.button1:hover {
  background-color: rgb(0, 255, 55);
  color: rgb(255, 255, 255);
}

li {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin: 9px 16px;
  font-size: 12px;
  color: rgba(87, 125, 139, 0.623);
}

.button2 {
  color: black;
  background: rgba(151, 187, 194, 0.644);
  border-color: cornsilk;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 10px;
  border-color: rgb(241, 238, 238);
}

.button2:hover {
  background-color: red;
  color: rgb(255, 255, 255);
}
